import { atom } from "recoil";
import { recoilPersist } from "recoil-persist";
import { music1, music2, music3 } from "./utils/data/defaultMusic";

export interface IControllerSetting {
  loop: number;
  isRandom: boolean;
};

export interface ITrackInfo {
  tid: number;
  name: string;
  duration: number;
  preview: string;
  arid: number;
  artist: string;
  alid: number;
  album?: string;
  imageSm: string;
  imageLg: string;
  rank?: number;
  index?: number;
};

export interface INowPlaying {
  index: number;
  track: ITrackInfo;
}

export interface IPlayListElement {
  [key: number]: ITrackInfo;
}

const { persistAtom } = recoilPersist({
  key: "cocoMusicPersist",
  storage: localStorage,
});

/**
 * [] 
 */
export const isLoggedInState = atom({
  key: "cocoLoggedIn",
  default: false,
  effects_UNSTABLE: [persistAtom],
});

/**
 * [ ]   
 */
export const nowPlayingState = atom<INowPlaying>({
  key: "nowPlaying",
  default: {
    index: 0,
    track: music1,
  },
  // effects_UNSTABLE: [persistAtom],
});

/**
 * [ ]
 */
export const playListState = atom<IPlayListElement>({
  key: "playList",
  default: {
    0: music1,
    1: music2,
    2: music3,
  },
  // effects_UNSTABLE: [persistAtom],
});

/**
 * [,  ]  
 */
export const controllerSettingState = atom<IControllerSetting>({
  key: "controllerSetting",
  // [loop] 1:   , 2:    , 3:    
  default: {
    loop: 1,
    isRandom: false,
  },
  // effects_UNSTABLE: [persistAtom],
});






/**
 *    
 */
export const isDarkThemeState = atom({
  key: "defaultTheme",
  default: false,
});